<template>
  <div class="my-trips-wrapper">
    <NavBar />
    
    <div class="container py-5">
      <div class="d-flex justify-content-between align-items-center mb-4">
        <h2>ÊàëÁöÑÊóÖË°åËÆ°Âàí</h2>
        <button class="btn btn-primary" @click="createNew">
          <i class="bi bi-plus-circle me-2"></i>ÂàõÂª∫Êñ∞ÊóÖË°å
        </button>
      </div>
      
      <!-- LoadingÁä∂ÊÄÅ -->
      <div v-if="loading" class="text-center py-5">
        <div class="spinner-border text-primary" role="status">
          <span class="visually-hidden">Âä†ËΩΩ‰∏≠...</span>
        </div>
      </div>
      
      <!-- ÊóÖË°åÂàóË°® -->
      <div v-else-if="trips.length > 0" class="row">
        <div v-for="trip in trips" :key="trip.id" class="col-md-6 col-lg-4 mb-4">
          <div class="trip-card">
            <div class="trip-header" :style="{ background: trip.theme_color || '#f0e68c' }">
              <div class="trip-icon">{{ trip.icon }}</div>
              <div class="trip-meta">
                <span class="badge" :class="trip.status === 'published' ? 'bg-success' : 'bg-secondary'">
                  {{ trip.status === 'published' ? 'Â∑≤ÂèëÂ∏É' : 'ËçâÁ®ø' }}
                </span>
                <span class="badge ms-2" :class="trip.visibility === 'public' ? 'bg-info' : 'bg-warning'">
                  {{ trip.visibility === 'public' ? 'ÂÖ¨ÂºÄ' : 'ÁßÅÊúâ' }}
                </span>
              </div>
            </div>
            <div class="trip-body">
              <h5 class="trip-title">{{ trip.title }}</h5>
              <p class="trip-desc text-muted">{{ trip.description || 'ÊöÇÊó†ÊèèËø∞' }}</p>
              <div class="trip-info">
                <small class="text-muted">
                  <i class="bi bi-calendar me-1"></i>{{ trip.days_count || 0 }}Â§©
                </small>
                <small class="text-muted ms-3">
                  <i class="bi bi-clock me-1"></i>{{ formatDate(trip.updated_at) }}
                </small>
              </div>
            </div>
            <div class="trip-actions">
              <button class="btn btn-sm btn-outline-primary" @click="editTrip(trip.slug)">
                <i class="bi bi-pencil me-1"></i>ÁºñËæë
              </button>
              <button class="btn btn-sm btn-outline-secondary" @click="viewTrip(trip.slug)">
                <i class="bi bi-eye me-1"></i>È¢ÑËßà
              </button>
              <button 
                v-if="trip.isOnTree" 
                class="btn btn-sm btn-danger" 
                @click="removeFromTree(trip.slug)">
                <i class="bi bi-x-circle me-1"></i>Êëò‰∏ãÊûúÂÆû
              </button>
              <button 
                v-else
                class="btn btn-sm btn-success" 
                @click="addToTree(trip.slug)">
                <i class="bi bi-tree me-1"></i>ËøêÁî®Âà∞ÊóÖË°åÊ†ë
              </button>
              <button class="btn btn-sm btn-outline-secondary" @click="showAdvancedSettings(trip.slug)" title="È´òÁ∫ßÈÄâÈ°π">
                ‚öôÔ∏è
              </button>
            </div>
          </div>
        </div>
      </div>
      
      <!-- Á©∫Áä∂ÊÄÅ -->
      <div v-else class="empty-state text-center py-5">
        <div class="empty-icon mb-3">üìù</div>
        <h4>ËøòÊ≤°ÊúâÊóÖË°åËÆ°Âàí</h4>
        <p class="text-muted mb-4">ÂºÄÂßãÂàõÂª∫‰Ω†ÁöÑÁ¨¨‰∏Ä‰∏™ÊóÖË°åËÆ°ÂàíÂêßÔºÅ</p>
        <button class="btn btn-primary btn-lg" @click="createNew">
          <i class="bi bi-plus-circle me-2"></i>ÂàõÂª∫Êñ∞ÊóÖË°å
        </button>
      </div>
    </div>
    
    <!-- È´òÁ∫ßËÆæÁΩÆÊ®°ÊÄÅÊ°Ü -->
    <AdvancedSettingsModal
      :show="showModal"
      title="‚öôÔ∏è È´òÁ∫ßÈÄâÈ°π"
      warning-text="Âà†Èô§ÊóÖË°åËÆ°ÂàíÂ∞Ü‰ºöÊ∞∏‰πÖÂà†Èô§ÊÇ®ÁöÑÊâÄÊúâÊï∞ÊçÆÔºåÂåÖÊã¨Ôºö"
      :warning-items="[
        'ÊâÄÊúâË°åÁ®ãÂÆâÊéí',
        'ÊâÄÊúâËØÑËÆ∫ÂíåÂõûÂ§ç',
        'ÊâÄÊúâÁªüËÆ°Êï∞ÊçÆ',
        'ÊâÄÊúâÂõæÁâáÂíåËßÜÈ¢ë'
      ]"
      action-button-text="üóëÔ∏è Âà†Èô§ÊóÖË°åËÆ°Âàí"
      @close="closeModal"
      @confirm="confirmDeleteTrip"
    />
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { useUserStore } from '@/stores'
import { getMyTrips, deleteTripPlan, addTripToTree, removeTripFromTree } from '@/api/tripPlan'
import { getTripList } from '@/api/trip'
import NavBar from '@/components/NavBar.vue'
import AdvancedSettingsModal from '@/components/AdvancedSettingsModal.vue'

export default {
  name: 'MyTripsView',
  
  components: {
    NavBar,
    AdvancedSettingsModal
  },
  
  setup() {
    const router = useRouter()
    const userStore = useUserStore()
    
    const trips = ref([])
    const loading = ref(true)
    const showModal = ref(false)
    const currentTripSlug = ref(null)
    
    const fetchMyTrips = async () => {
      loading.value = true
      try {
        const data = await getMyTrips()
        const tripsList = data.results || data || []
        
        trips.value = tripsList
        await checkTreeStatus() // Ê£ÄÊü•ÊòØÂê¶Âú®ÊóÖË°åÊ†ë‰∏≠
      } catch (error) {
        console.error('Ëé∑ÂèñÊóÖË°åÂ§ßÂéÖÂ§±Ë¥•:', error)
        alert('Âä†ËΩΩÂ§±Ë¥•')
      } finally {
        loading.value = false
      }
    }
    
    const createNew = () => {
      router.push('/editor/new')
    }
    
    const editTrip = (slug) => {
      router.push(`/editor/${slug}`)
    }
    
    const viewTrip = (slug) => {
      router.push(`/trip/${slug}/`)
    }
    
    // ÊòæÁ§∫È´òÁ∫ßËÆæÁΩÆÊ®°ÊÄÅÊ°Ü
    const showAdvancedSettings = (slug) => {
      if (!slug) {
        alert('ÈîôËØØÔºöËØ•ÊóÖË°åËÆ°ÂàíÁº∫Â∞ëÊ†áËØÜÁ¨¶')
        return
      }
      currentTripSlug.value = slug
      showModal.value = true
    }
    
    // ÂÖ≥Èó≠Ê®°ÊÄÅÊ°Ü
    const closeModal = () => {
      showModal.value = false
      currentTripSlug.value = null
    }
    
    // Á°ÆËÆ§Âà†Èô§ÊóÖË°åËÆ°Âàí
    const confirmDeleteTrip = () => {
      const slug = currentTripSlug.value
      
      if (!slug) {
        alert('ÈîôËØØÔºöÊó†Ê≥ïËé∑ÂèñÊóÖË°åËÆ°ÂàíÊ†áËØÜ')
        closeModal()
        return
      }
      
      if (!confirm('‚ö†Ô∏è ËØ∑ÂÜçÊ¨°Á°ÆËÆ§ÔºöÊÇ®Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ÊóÖË°åËÆ°ÂàíÂêóÔºü\n\nÊ≠§Êìç‰ΩúÊó†Ê≥ïÊí§ÈîÄÔºÅ')) {
        return
      }
      
      deleteTrip(slug)
      closeModal()
    }
    
    // Âà†Èô§ÊóÖË°åËÆ°Âàí
    const deleteTrip = async (slug) => {
      if (!slug) {
        alert('ÈîôËØØÔºöÊó†Ê≥ïËé∑ÂèñÊóÖË°åËÆ°ÂàíÊ†áËØÜ')
        return
      }
      
      try {
        await deleteTripPlan(slug)
        alert('Âà†Èô§ÊàêÂäü')
        await fetchMyTrips()
      } catch (error) {
        console.error('Âà†Èô§Â§±Ë¥•:', error)
        alert('Âà†Èô§Â§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
      }
    }
    
    const addToTree = async (slug) => {
      if (!confirm('Á°ÆÂÆöË¶ÅÂ∞ÜÊ≠§ÊóÖË°åËÆ°ÂàíÊ∑ªÂä†Âà∞ÊóÖË°åÊ†ëÂêóÔºüÂÆÉÂ∞ÜÊòæÁ§∫Âú®È¶ñÈ°µÁöÑÊóÖË°åÊ†ë‰∏ä„ÄÇ')) {
        return
      }
      
      try {
        const result = await addTripToTree(slug)
        alert(result.detail || 'ÊàêÂäüÊ∑ªÂä†Âà∞ÊóÖË°åÊ†ëÔºÅ')
        await fetchMyTrips() // Âà∑Êñ∞ÂàóË°®
      } catch (error) {
        console.error('Ê∑ªÂä†Âà∞ÊóÖË°åÊ†ëÂ§±Ë¥•:', error)
        alert('Ê∑ªÂä†Âà∞ÊóÖË°åÊ†ëÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï')
      }
    }
    
    const removeFromTree = async (slug) => {
      if (!confirm('Á°ÆÂÆöË¶ÅÂ∞ÜÊ≠§ÊóÖË°åËÆ°Âàí‰ªéÊóÖË°åÊ†ëÊëò‰∏ãÂêóÔºüÂÆÉÂ∞Ü‰∏çÂÜçÊòæÁ§∫Âú®È¶ñÈ°µÁöÑÊóÖË°åÊ†ë‰∏ä„ÄÇ')) {
        return
      }
      
      try {
        const result = await removeTripFromTree(slug)
        alert(result.detail || 'ÊàêÂäüÊëò‰∏ãÊûúÂÆûÔºÅ')
        await fetchMyTrips() // Âà∑Êñ∞ÂàóË°®
      } catch (error) {
        console.error('Êëò‰∏ãÊûúÂÆûÂ§±Ë¥•:', error)
        alert('Êëò‰∏ãÊûúÂÆûÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï')
      }
    }
    
    const checkTreeStatus = async () => {
      try {
        const treeData = await getTripList()
        const treeSlugs = new Set((treeData.results || treeData || []).map(t => t.slug))
        
        // ‰∏∫ÊØè‰∏™tripÊ∑ªÂä†isOnTreeÂ±ûÊÄß
        trips.value.forEach(trip => {
          trip.isOnTree = treeSlugs.has(trip.slug)
        })
      } catch (error) {
        console.error('Ëé∑ÂèñÊóÖË°åÊ†ëÁä∂ÊÄÅÂ§±Ë¥•:', error)
      }
    }
    
    const formatDate = (dateStr) => {
      if (!dateStr) return 'ÊöÇÊó†'
      const date = new Date(dateStr)
      return date.toLocaleDateString('zh-CN')
    }
    
    onMounted(() => {
      if (!userStore.isLoggedIn) {
        alert('ËØ∑ÂÖàÁôªÂΩï')
        router.push('/login')
        return
      }
      fetchMyTrips()
    })
    
    return {
      trips,
      loading,
      showModal,
      createNew,
      editTrip,
      viewTrip,
      showAdvancedSettings,
      closeModal,
      confirmDeleteTrip,
      deleteTrip,
      addToTree,
      removeFromTree,
      formatDate
    }
  }
}
</script>

