}

/* ÂìçÂ∫îÂºè */
@media (max-width: 768px) {
  .trip-actions {
    flex-wrap: wrap;
  }
  
  .trip-actions .btn {
    flex: 1;
    min-width: 80px;
  }
}
</style>

